import React, { ChangeEvent, KeyboardEvent, useState } from 'react';
import { ValueFilter } from './App';
//
type TaskType = {
	id: string;
	title: string;
	isDone: boolean;
};
//
type PropsType = {
	title: string;
	tasks: Array<TaskType>;
	removeTask: (id: string) => void;
	setButtonValue: (el: ValueFilter) => void;
	addTask: (v: string) => void;
	filter: string;
	changeCheckBox: (id: string, bool: boolean) => void;
};
//
export function Todolist(props: PropsType) {
	const liItem = props.tasks.map((t) => {
		const removeTask = () => props.removeTask(t.id);
		return (
			<li className={t.isDone ? 'done' : ''}>
				<input
					className='inpT'
					type='checkbox'
					checked={t.isDone}
					onChange={(e) => props.changeCheckBox(t.id, e.target.checked)}
				/>
				<span>{t.title}</span>
				<button className='btnLi' onClick={removeTask}>
					X
				</button>
			</li>
		);
	});
	//
	let [valueInput, setValueInput] = useState('');
	//
	const all = () => props.setButtonValue('all');
	const active = () => props.setButtonValue('active');
	const completed = () => props.setButtonValue('completed');
	//
	const keyPress = (el: KeyboardEvent<HTMLInputElement>) => {
		if (el.key === 'Enter' && valueInput.trim() !== '') {
			props.addTask(valueInput);
			setValueInput('');
		}
	};
	//
	const btnPress = () => {
		if (valueInput.trim() === '') {
			return;
		}
		props.addTask(valueInput);
		setValueInput('');
	};
	//
	const onChangeValue = (e: ChangeEvent<HTMLInputElement>) => {
		setValueInput(e.currentTarget.value);
	};
	return (
		<div>
			<h3>{props.title}</h3>
			<div>
				<input
					onChange={onChangeValue}
					value={valueInput}
					onKeyPress={keyPress}
					className='input'
				/>
				<button className='btn' onClick={btnPress}>
					+
				</button>
			</div>
			<ul>{liItem}</ul>
			<div>
				<button
					className={`${props.filter === 'all' ? 'active' : ''} ${'btnPanel'}`}
					onClick={all}
				>
					All
				</button>
				<button
					className={`${
						props.filter === 'active' ? 'active' : ''
					} ${'btnPanel'}`}
					onClick={active}
				>
					Active
				</button>
				<button
					className={`${
						props.filter === 'completed' ? 'active' : ''
					} ${'btnPanel'}`}
					onClick={completed}
				>
					Completed
				</button>
			</div>
		</div>
	);
}
