import React, { useState } from 'react';
import { v1 } from 'uuid';
import './App.css';
import { Todolist } from './Todolist';
export type ValueFilter = 'all' | 'completed' | 'active';
function App() {
	let [filter, setFilter] = useState<ValueFilter>('all');
	//
	let [tasks, setTasks] = useState([
		{ id: v1(), title: 'HTML&CSS', isDone: true },
		{ id: v1(), title: 'JS', isDone: true },
		{ id: v1(), title: 'ReactJS', isDone: false },
		{ id: v1(), title: 'Redux', isDone: false },
		{ id: v1(), title: 'nodeJS', isDone: false },
	]);
	//
	function tasksState() {
		if (filter === 'completed') {
			return tasks.filter((f) => f.isDone === true);
		}
		if (filter === 'active') {
			return tasks.filter((f) => f.isDone === false);
		} else {
			return tasks;
		}
	}
	//
	function setButtonValue(el: ValueFilter) {
		setFilter(el);
	}
	//
	function removeTask(id: string) {
		let tasksChange = tasks.filter((f) => {
			return f.id !== id;
		});
		setTasks(tasksChange);
	}
	//
	function addTask(v: string) {
		let newTask = { id: v1(), title: v, isDone: false };
		let newTasks = [newTask, ...tasks];
		setTasks(newTasks);
	}
	//
	function changeCheckBox(id:string,bool:boolean) {
		const newStatus = tasks.map((t) => t.id === id ? {...t,isDone:bool} : t)
		setTasks(newStatus)
	}
	return (
		<div className='App'>
			<Todolist
			filter={filter}
				title='What to learn'
				tasks={tasksState()}
				removeTask={removeTask}
				setButtonValue={setButtonValue}
				addTask={addTask}
				changeCheckBox={changeCheckBox}
			/>
		</div>
	);
}

export default App;
